<template>
  <div class="message-image">
    <img
      mode="aspectFit"
      class="message-image"
      :src="url"
    >
  </div>
</template>

<script lang="ts" setup>
import vue from '../../../../adapter-vue';
import { CUSTOM_BIG_EMOJI_URL } from '../../emoji-config';
const { ref, onMounted } = vue;

const props = defineProps({
  content: {
    type: Object,
    default: () => ({}),
  },
});

const url = ref(props.content.url);

onMounted(() => {
  if (props.content.type === 'custom') {
    if (!CUSTOM_BIG_EMOJI_URL) {
      uni.__f__('warn','at pages-ai-desk/ai-desk-customer-uniapp/components/CustomerServiceChat/message-list/message-elements/message-face.vue:28',
        'CUSTOM_BIG_EMOJI_URL is required for custom emoji, please check your CUSTOM_BIG_EMOJI_URL.',
      );
    } else {
      url.value = CUSTOM_BIG_EMOJI_URL + props.content.name;
    }
  }
});
</script>

<style lang="scss" scoped>
@import "../../style/common";

.message-image {
  width: 80px;
  height: 80px;
}
</style>
